<template>
  <div class="test-page">
    <div v-if="isLoggedIn">
      <div v-if="face">
        <Face />
      </div>
    </div>
    <div v-else>
      <div class="not-login fw-bold text-center">
        <p>You must login to continue</p>
        <a
          role="button"
          class="btn border-0 p-0"
          data-bs-toggle="modal"
          data-bs-target="#exampleModal1"
        >
          <i class="fa-solid fa-right-to-bracket"></i>
          {{ $t('buttons.login') }}
        </a>
      </div>
    </div>
  </div>
</template>

<script>
import Face from '@/components/test/Face.vue'
export default {
  data() {
    return {
      face: false,
      body: false,
      style: false,
    }
  },
  mounted() {
    this.openTab(this.tabOpen)
  },
  computed: {
    tabOpen() {
      return this.$route.params.slug
    },
    isLoggedIn() {
      return this.$store.getters.isLoggedIn
    },
  },
  watch: {
    $route() {
      this.openTab(this.$route.params.slug)
    },
  },
  methods: {
    openTab(tab) {
      this.dashboard = false
      this.orders = false
      this.addresses = false
      this.wishlist = false
      this.downloads = false
      this.payment = false
      this.accountDetails = false
      this[tab] = true
    },
    logout() {
      this.$store.dispatch('logout')
    },
  },
  components: { Face },
}
</script>

<style lang="scss">
.test-page {
  min-height: calc(100vh - 300px);
  padding: 20px 0 100px;
}
</style>
