<template>
  <form @submit.prevent="login">
    <div class="mb-3">
      <label for="email" class="form-label">
        {{ $t('inputs.Email address') }}
      </label>
      <input
        type="email"
        class="form-control"
        id="email"
        v-model="email"
        required
        :placeholder="$t('misc.Enter your email')"
      />
    </div>
    <div class="mb-3">
      <label for="password" class="form-label">
        {{ $t('inputs.password') }}
      </label>
      <input
        type="password"
        class="form-control"
        id="password"
        v-model="password"
        required
        :placeholder="$t('misc.Enter your password')"
      />
    </div>
    <div>
      <a href="#" role="button" class="text-white mb-3 d-block">
        {{ $t('misc.Forgot password?') }}
      </a>
    </div>
    <div v-if="msg">
      <p class="errMsg">
        {{ msg }}
      </p>
    </div>

    <button type="submit" class="btn-bg-primary d-block mx-auto">
      {{ $t('buttons.login') }}
    </button>
  </form>
  <a
    role="button"
    class="btn border-0 p-0 d-block text-white mt-4"
    data-bs-toggle="modal"
    data-bs-target="#exampleModal2"
  >
    {{ $t('misc.Create an account') }}
  </a>
</template>

<script>
export default {
  data() {
    return {
      email: '',
      passsword: '',
    }
  },
  methods: {
    login() {
      this.$store.dispatch('login', {
        email: this.email,
        password: this.password,
      })
    },
  },
  computed: {
    msg() {
      return this.$store.getters.status
    },
  },
}
</script>

