<template>
  <form @submit.prevent="register">
    <div class="mb-3">
      <label for="name" class="form-label">
        {{ $t('inputs.name') }}
      </label>
      <input
        type="text"
        class="form-control"
        id="name"
        v-model="name"
        :placeholder="$t('misc.Enter your name')"
      />
      <div v-for="error of v$.name.$errors" :key="error.$uid">
        <div class="error-msg">
          {{ $t(`misc.${error.$message}`) }}
        </div>
      </div>
    </div>
    <div class="mb-3">
      <label for="email" class="form-label">
        {{ $t('inputs.Email address') }}
      </label>
      <input
        type="email"
        class="form-control"
        id="email"
        v-model="email"
        :placeholder="$t('misc.Enter your email')"
      />
      <div v-for="error of v$.email.$errors" :key="error.$uid">
        <div class="error-msg">
          {{ $t(`misc.${error.$message}`) }}
        </div>
      </div>
    </div>
    <div class="mb-3">
      <label for="password" class="form-label">
        {{ $t('inputs.password') }}
      </label>
      <input
        type="password"
        class="form-control"
        v-model="password"
        id="password"
        :placeholder="$t('misc.Enter Your Password')"
      />
      <div v-for="error of v$.password.$errors" :key="error.$uid">
        <div class="error-msg">
          {{ $t(`misc.${error.$message}`) }}
        </div>
      </div>
    </div>
    <div class="mb-3">
      <label for="confirmpassword" class="form-label">
        {{ $t('inputs.confirm password') }}
      </label>
      <input
        type="password"
        class="form-control"
        id="confirmpassword"
        v-model="cPassword"
        :placeholder="$t('misc.Enter confirm password')"
      />
      <div v-for="error of v$.cPassword.$errors" :key="error.$uid">
        <div class="error-msg">
          {{ $t(`misc.${error.$message}`) }}
        </div>
      </div>
    </div>
    <div v-if="msg">
      <p class="errMsg">
        {{ msg }}
      </p>
    </div>

    <button type="submit" class="btn-bg-primary d-block mx-auto">
      {{ $t('buttons.signUp') }}
    </button>
  </form>
  <div class="mt-4 d-flex align-items-center text-white">
    <p class="mb-0">
      {{ $t('misc.Dont have an account?') }}
    </p>
    <a
      role="button"
      class="btn border-0 p-0 text-white mx-2"
      data-bs-toggle="modal"
      data-bs-target="#exampleModal1"
    >
      {{ $t('buttons.login') }}
    </a>
  </div>
</template>

<script>
import useVuelidate from '@vuelidate/core'
import { required, email, sameAs } from '@vuelidate/validators'
export default {
  setup() {
    return { v$: useVuelidate() }
  },

  data() {
    return {
      name: '',
      email: '',
      password: '',
      cPassword: '',
    }
  },
  computed: {
    msg() {
      return this.$store.getters.status
    },
  },
  methods: {
    async register() {
      const result = await this.v$.$validate()
      const form = new FormData()
      form.append('name', this.name)
      form.append('email', this.email)
      form.append('password', this.password)
      if (!result) {
        return
      }
      this.$store.dispatch('register', form)
    },
  },
  validations() {
    return {
      name: { required }, // Matches this.firstName
      email: { required, email }, // Matches this.lastName
      password: { required },
      cPassword: {
        required,
        sameAsPassword: sameAs(this.password),
      },
    }
  },
}
</script>

<style></style>
