<template>
  <section class="face-test">
    <h2 class="title">Face Test</h2>
    <div class="container">
      <div class="row align-items-center">
        <div class="col-md-5">
          <div class="img-container text-center">
            <img src="@/assets/face.png" alt="face" />
          </div>
        </div>
        <div class="col-md-7">
          <div class="form" @submit.prevent="submit">
            <form>
              <div class="row g-3 mb-4 align-items-center tooltip-parent">
                <span class="tooltip">
                  From the front bone to the opposite front bone
                </span>
                <div class="col-4">
                  <label for="forHead" class="col-form-label">
                    ForeHead Size
                  </label>
                </div>
                <div class="col-8">
                  <input
                    type="number"
                    placeholder="Enter in cm"
                    id="forHead"
                    class="form-control"
                    v-model="foreHead"
                    required
                  />
                </div>
              </div>
              <div class="row g-3 mb-4 align-items-center tooltip-parent">
                <span class="tooltip">
                  From outside the eye to the outside of the corresponding eye
                </span>
                <div class="col-4">
                  <label for="Cheek Size" class="col-form-label">
                    Cheek Size
                  </label>
                </div>
                <div class="col-8">
                  <input
                    type="number"
                    placeholder="Enter in cm"
                    id="Cheek Size"
                    class="form-control"
                    v-model="cheek"
                    required
                  />
                </div>
              </div>
              <div class="row g-3 mb-4 align-items-center tooltip-parent">
                <span class="tooltip">
                  from the hairline through the nose to the chin
                </span>
                <div class="col-4">
                  <label for="Face length" class="col-form-label">
                    Face length Size
                  </label>
                </div>
                <div class="col-8">
                  <input
                    type="number"
                    placeholder="Enter in cm"
                    id="Face length"
                    class="form-control"
                    v-model="faceLength"
                    required
                  />
                </div>
              </div>
              <div class="row g-3 mb-4 align-items-center tooltip-parent">
                <span class="tooltip">
                  from the jawbone to the middle of the chin, and multiply it in
                  two folds
                </span>
                <div class="col-4">
                  <label for="Jaw" class="col-form-label">
                    Jaw Size
                  </label>
                </div>
                <div class="col-8">
                  <input
                    type="number"
                    placeholder="Enter in cm"
                    id="Jaw"
                    class="form-control"
                    v-model="jaw"
                    required
                  />
                </div>
              </div>
              <div class="form-group">
                <div class="row">
                  <div class="col-6">
                    <label class="col-form-label">
                      Chin Shape
                    </label>
                  </div>
                  <div class="col-6 d-flex align-items-center">
                    <div class="row">
                      <div class="col">
                        <div class="form-check">
                          <input
                            class="form-check-input"
                            type="radio"
                            id="Sharp"
                            value="Sharp"
                            v-model="chinShape"
                            required
                          />
                          <label class="form-check-label" for="Sharp">
                            Sharp
                          </label>
                        </div>
                      </div>
                      <div class="col">
                        <div class="form-check">
                          <input
                            class="form-check-input"
                            type="radio"
                            id="tapered"
                            value="tapered"
                            v-model="chinShape"
                            required
                          />
                          <label class="form-check-label" for="tapered">
                            tapered
                          </label>
                        </div>
                      </div>
                    </div>
                  </div>
                </div>
              </div>
              <div class="form-group">
                <div class="row">
                  <div class="col-6">
                    <label class="col-form-label">
                      Is there a Salience in the jaw?
                    </label>
                  </div>
                  <div class="col-6 d-flex align-items-center">
                    <div class="row">
                      <div class="col">
                        <div class="form-check">
                          <input
                            class="form-check-input"
                            type="radio"
                            id="Yes"
                            v-model="salienceRadio"
                            value="yes"
                            required
                          />
                          <label class="form-check-label" for="Yes">
                            Yes
                          </label>
                        </div>
                      </div>
                      <div class="col">
                        <div class="form-check">
                          <input
                            class="form-check-input"
                            type="radio"
                            id="No"
                            v-model="salienceRadio"
                            value="no"
                            required
                          />
                          <label class="form-check-label" for="No">
                            No
                          </label>
                        </div>
                      </div>
                    </div>
                  </div>
                </div>
              </div>
              <div>
                <button type="submit" class="btn-bg-primary w-100 mt-5">
                  {{ $t('buttons.send') }}
                </button>
              </div>
            </form>
          </div>
        </div>
      </div>
    </div>
  </section>
</template>

<script>
export default {
  data() {
    return {
      foreHead: '',
      cheek: '',
      faceLength: '',
      jaw: '',
      chinShape: 'Sharp',
      salienceRadio: 'yes',
    }
  },
  methods: {
    submit() {
      const frm = new FormData()
      frm.append('fore_head', this.foreHead)
      frm.append('cheek', this.cheek)
      frm.append('face_length', this.faceLength)
      frm.append('jaw', this.jaw)
      frm.append('chin_shape', this.chinShape)
      frm.append('salience_radio', this.salienceRadio)

      this.axios.post('auth/face-test', frm).then((data) => {
        console.log(data)
      })
    },
  },
}
</script>

<style lang="scss">
.face-test {
  padding: 50px 0;
  .img-container {
    img {
      max-width: 300px;
    }
  }
  .form {
    padding: 30px;
  }
}
.tooltip-parent {
  position: relative;
  .tooltip {
    // display: none;
    position: absolute;
    top: 0px;
    left: 50%;
    transform: translateX(-50%);
    border-radius: 15px;
    background: #7d704e;
    transition: all 0.3s;
    text-align: center;
    color: #fff;
    opacity: 0;
    &::before {
      content: '';
      border: 10px solid transparent;
      border-top: 10px solid #7d704e;
      bottom: -20px;
      left: 50%;
      position: absolute;
      transform: translateX(-50%);
    }
  }
  &:hover {
    .tooltip {
      opacity: 1;
      top: -45px;
    }
  }
}
</style>
