<template>
  <a
    role="button"
    class="btn border-0 p-0 text-white fw-bold locale"
    @click="switchLang"
  >
    {{ $t(`locales.${lang}`) }}
  </a>
</template>

<script>
import Cookies from 'js-cookie'

export default {
  data: () => ({
    lang: '',
  }),

  mounted() {
    this.lang = this.$i18n.locale === 'ar' ? 'en' : 'ar'
    console.log(this.$i18n.locale)
  },

  methods: {
    switchLang() {
      Cookies.set('locale', this.lang)
      setTimeout(() => {
        window.location.reload()
      }, 300)
    },
  },
}
</script>
<style>
.locale {
  line-height: 1;
  font-size: 14px;
}
</style>
